// Scrollbars 

// Firefox
* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155,0.5) transparent;
}

// Chrome & Edge
*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(155,155,155,0.5);
    border-radius: 20px;
    border: transparent; 
}

// Library & elements

.library {
    width: 20rem;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #E72F37;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: hidden;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2 {
        padding: 2rem;
    }
}

.library-song {
    display: flex;
    align-items: center; 
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    transition: background 0.5s ease;
    h3 {
        color: #FFFFFF;
    }
    img {
        width: 30%;
    }
    &:hover {
        background-color: #FFFFFF;
        h3 {
            color: #E72F37;
        }
    }
}

.song-description {
    padding: 1rem;
    h3 {
        font-size: 1rem;
    }
    h4 {
        font-size: 0.7rem;
    }
}

.selected {
    background: #FFFFFF;
    h3 {
        color: #E72F37;
    }
}

.active-library {
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library {
      width: 100%;
    }
}